.App {
  text-align: center;
  //background-Image: url(./Img/background.jpeg);
  //background-size: 'cover';
  //padding-bottom: '20px';
 // min-height: '100vh';
  //position: 'relative';
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header{
  .nav-menu {
   
    & .item {
      list-style-type: none;
      padding: 15px;
    }

    .site{
      float: left;
    }
    
    display: flex;
    flex-direction: row;
    float: right;
  }

}